import d3 from 'd3';
import * as chromaScale from 'd3-scale-chromatic';
import { curry } from 'lodash';
import { setupElement, fontStyle } from '../utils/';
import { hideTooltip, showTooltip } from '../utils/tooltip';
import { TEXT_MIDDLE } from '../utils/constants';

function areaOver() {
  d3.select(this.parentNode).style({ opacity: 0.8 });
}

function areaOut() {
  d3.select(this.parentNode).style({ opacity: 1 });
}

function relevantAreasUpdate(selection, selectors, accessors, options, scale, base) {
  selection
    .on('mousemove', function(datum){
      showTooltip(
        selectors,
        accessors,
        options,
        base, // stands for { xScale, yScale }
        null, // serie ... ?
        datum,
        options.colors[0]
      );
    })
    .on('mouseleave', curry(hideTooltip)(selectors))
    .on('mouseover', areaOver)
    .on('mouseout', areaOut)
    .style({
      fill: d => scale(accessors.value(d))
    });
}

function labelEnter(selection, selectors) {
  setupElement(selection, 'svg:text', selectors.label);
}

function labelUpdate(selection, options, accessors, projection) {
  selection.text(accessors.label)
  selection.attr({
    x: d => d3.geo.path().projection(projection).centroid(d)[0],
    y: d => d3.geo.path().projection(projection).centroid(d)[1],
  });
  selection.style({
    ...fontStyle(accessors.labelFont(options)),
    display: accessors.labelDisplay(options),
    'text-anchor': TEXT_MIDDLE
  })
}

export default function eauors(target) {
  target.eauors = {
    ...target.eauors,
    relevantAreasUpdate,
    labelEnter,
    labelUpdate,
  };
}


