import { get } from 'lodash';

export default function accessors(target) {
  target.accessors = {
    ...target.accessors,
    colors: options => get(options, 'serie.choropleth.colors'),
    labelDisplay: options => get(options, 'serie.choropleth.labelDisplay'),
    labelFont: options => get(options, 'serie.choropleth.labelFont'),
    maxDomainOptions: options => get(options, 'serie.choropleth.maxDomain'),
    minDomainOptions: options => get(options, 'serie.choropleth.minDomain'),
    value: area => get(area, 'properties.value', null)
  }
}


