import { get, last, max, size } from 'lodash';
import { getFormat, getRangeDelta, getScaleExtent } from '../utils';

export default function accessors(target) {
  target.accessors = {
    ...target.accessors,
    data: data => data || [],
    datapoints: serie => get(serie, 'datapoints', []),
    lastDatapoint: serie => last(target.accessors.datapoints(serie)),
    yRange: (base, axis) => base.xScale.range(),
    annotationDatapoint: (options, datum) => getFormat(options.format.datapoint)(datum.y),
    isCondensed: (options, serie, scale) => {
      const datapointsCount = size(target.accessors.datapoints(serie));
      const rangeDelta = getRangeDelta(getScaleExtent(scale));
      const ratio = rangeDelta / max([1, datapointsCount]);
      return ratio < options.line.densityRatio;
    }
  };
}



