import { get } from 'lodash';

export default function accessors(target) {
  target.accessors = {
    ...target.accessors,
    areas: data => get(data, 'objects.areas', {}),
    areaColor: options => get(options, 'map.defaultAreaColor'),
    label: d => get(d, 'properties.label', null),
    scale: options => get(options, 'map.scale'),
    width: options => get(options, 'base.width'),
    height: options => get(options, 'base.height'),
    extBoundariesColor: options => get(options, 'map.extBoundariesColor'),
    extBoundariesThickness: options => get(options, 'map.extBoundariesThickness'),
    extBoundariesDisplay: options => get(options, 'map.extBoundariesDisplay'),
    intBoundariesColor: options => get(options, 'map.intBoundariesColor'),
    intBoundariesThickness: options => get(options, 'map.intBoundariesThickness'),
    intBoundariesDisplay: options => get(options, 'map.intBoundariesDisplay'),
    projection: options => get(options, 'map.projection')
  };
}


