import d3 from 'd3';
import * as chroma from 'd3-scale-chromatic';
import { filter, isUndefined, map, max, min } from 'lodash';
import compose from 'lodash.compose';

export default function computors(target) {
  target.computors = {
    ...target.computors,
    values: data => filter(
      map(
        target.accessors.areas(data).geometries,
        area => target.accessors.value(area)
      ),
      value => value !== null
    ),
    domain: (options, data) => compose(
      domainValues => {
        const minDomainOptions = target.accessors.minDomainOptions(options);
        const maxDomainOptions = target.accessors.maxDomainOptions(options);

        return [
          (!isUndefined(minDomainOptions) && minDomainOptions < domainValues[0]) ?
            minDomainOptions : domainValues[0],
          (!isUndefined(maxDomainOptions) && maxDomainOptions > domainValues[1]) ?
            maxDomainOptions : domainValues[1]
        ];
      },
      values => [min(values), max(values)],
      target.computors.values
    )(data),
    colorScale: (options, data) => d3.scale
      .quantize().domain(target.computors.domain(options, data))
      .range(target.accessors.colors(options))
  }
}


