import { head, last, round, gt, includes, uniq, concat,
  min as _min, max as _max, isNull, range, get, ceil } from 'lodash';
import d3 from 'd3';
import { getDomainDelta, getFormat, getScaleType } from '../utils/';
import { DEFAULT_TICK_VALUES_COUNT, MAX_TICK_VALUES, LINEAR, ORDINAL } from '../utils/constants';

export function computeLinearScale(range, domain) {
  return d3.scale.linear().range(range).domain(domain);
}

export function computeLinearDomain(options, data, datumAccessor) {
  const extent = d3.extent(data, datumAccessor);
  let [min, max] = [
    _min([head(extent), options.linear.min]),
    _max([last(extent), options.linear.max])
  ];
  const pivot = options.linear.pivot.value;
  if (!isNull(pivot)) {
    if (pivot < min) min = pivot;
    if (pivot > max) max = pivot; 
  }
  if (min === max) {
    const delta = Math.abs(min);
    [min, max] = [min - delta, min + delta];
  }
  return [min, max];
}

export function computeTimeDomain(options, data, datumAccessor) {
  return d3.extent(data, datumAccessor);
}

export function computeStep(domain, step) {
  const delta = getDomainDelta(domain);
  if (!step || step === 0 || round(delta / step) > MAX_TICK_VALUES)
    return round(delta / DEFAULT_TICK_VALUES_COUNT);
  return step;
}

export function computeTickValues(domain, step, pivot) {
  const _step = computeStep(domain, step);
  const [ticksBelowPivot, ticksAbovePivot] = [[], []];
  let [tickBelowPivot, tickAbovePivot] = [pivot, pivot];

  while (tickBelowPivot > head(domain) || tickAbovePivot < last(domain)) {
    if (tickBelowPivot > head(domain)) {
      ticksBelowPivot.push(tickBelowPivot);
      tickBelowPivot -= _step;
    }

    if (tickAbovePivot < last(domain)) {
      ticksAbovePivot.push(tickAbovePivot);
      tickAbovePivot += _step;
    }
  }

  // while allows a n complexity but does not handle the edge cases without an infinite loop
  if (tickAbovePivot === last(domain)) ticksAbovePivot.push(tickAbovePivot);
  if (tickBelowPivot === head(domain)) ticksBelowPivot.push(tickBelowPivot);

  return uniq(concat([], ticksBelowPivot, ticksAbovePivot));
}

export function getLinearTickValues(scale, options, pivot) {
  const size = options.baseSize;
  const min = options.linear.minDisplaySize;
  
  return (size >= min && options.tick.display) ?
    computeTickValues(scale.domain(), options.linear.step, pivot) : [];
}

export function computeLinearAxis(scale, pivot, options) {
  return d3.svg.axis()
    .scale(scale)
    .orient(options.orient)
    .tickSize(options.tick.size, options.tick.size)
    .tickFormat(getFormat(options.format))
    .tickValues(getLinearTickValues(scale, options, pivot));
}

export function computeOrdinalScale(options, interval, values) {

  return d3.scale.ordinal().domain(values).rangeBands(interval, options.gap, options.padding);
}

function getOrdinalTickValues(scale, options) {
  const size = options.baseSize;
  const min = options.ordinal.minDisplaySize;

  return (size >= min && options.tick.display) ? scale.domain() : [];
}

export function computeOrdinalAxis(scale, options) {
  return d3.svg.axis()
    .scale(scale)
    .orient(options.orient)
    .tickSize(options.tick.size, options.tick.size)
    .tickFormat(getFormat(options.format))
    .tickValues(getOrdinalTickValues(scale, options));
}

export function computeTimeScale(range, domain) {
  return d3.time.scale.utc().range(range).domain(domain);
}

export function computeTimeAxis(scale, pivot, options) {
  return d3.svg.axis()
    .scale(scale)
    .orient(options.orient)
    .tickSize(options.tick.size, options.tick.size)
    .tickFormat(getFormat(options.format))
    .ticks(d3.time[options.linear.frequency], options.linear.step);
}

export function computeAreaIndex(areaIndex, domain, domainIndex) {
  return domainIndex - getDomainDelta(domain) * areaIndex;
}



