import { isString } from 'lodash';

export default function accessors(target) {
  target.accessors = {
    ...target.accessors,
    x: datum => isString(datum.x) ? new Date(datum.x) : datum.x,
    /*xTicks: (base, axis, options) => {
      return axis.xAxis.scale().ticks(d3.time[options.linear.frequency], options.linear.step)
    }*/
    xTicks: (base, axis, options) => {
      return axis.xAxis.scale().ticks()
    }
  };
}



