import d3 from 'd3';
import { curry, bind } from 'lodash';
import {
  setupElement, getColor, getOverColor, selector, fontStyle, fontBaselineOffset,
  getElementDimensions
} from '../utils/';
import {
  NONE, TEXT_ABOVE, TEXT_MIDDLE, TEXT_START, ANNOTATION_SERIE_OFFSET
} from '../utils/constants';
import { showTooltip, hideTooltip } from '../utils/tooltip';

function lineEnter(selection, options, selector) {
  return setupElement(selection, 'svg:path', selector);
}

function lineUpdate(selection, options, accessors, { xScale, yScale }) {
  const line = d3.svg.line()
    .x(datum => xScale(accessors.x(datum)))
    .y(datum => yScale(accessors.y(datum)));

  selection
    .attr('d', serie => line(accessors.datapoints(serie)))
    .style({
      stroke: serie => getColor(options, accessors.focusData(serie)),
      'stroke-width': serie => (
        accessors.isFocused(serie) ? options.line.focused.thickness : options.line.thickness
      ),
      fill: NONE
    });
}

function lineOver(options, selectors, accessors, scales, serie) {
  const color = getOverColor(options, accessors.focusData(serie));

  d3.select(this.parentNode)
    .select(selector(selectors.line))
    .style({ stroke: color, 'stroke-width': options.line.focused.thickness });
  if (!accessors.isCondensed(options, d3.select(this.parentNode).datum(), scales.xScale)) {
    d3.select(this.parentNode)
      .selectAll(selector(selectors.marker))
      .style({ fill: color, opacity: 1 });
    d3.select(this.parentNode)
      .selectAll(selector(selectors.annotationDatapoint))
      .style({ fill: color, opacity: 1 });
  }
}

function lineOut(options, selectors, accessors, scales, serie) {
  const color = getColor(options, accessors.focusData(serie));

  d3.select(this.parentNode)
    .select(selector(selectors.line))
    .style({
      stroke: color,
      'stroke-width': serie => (
        accessors.isFocused(serie) ? options.line.focused.thickness : options.line.thickness
      )
    });
  if (!accessors.isCondensed(options, d3.select(this.parentNode).datum(), scales.xScale)) {
    d3.select(this.parentNode)
      .selectAll(selector(selectors.marker))
      .style({
        fill: color,
        opacity: accessors.isFocused(serie) ? 1 : 0
      });
    d3.select(this.parentNode)
      .selectAll(selector(selectors.annotationDatapoint))
      .style({ fill: color, opacity: 0 });
  }
}

function hoverlineEnter(selection, options, selectors, accessors, scales) {
  lineEnter(selection, options, selectors.hoverline)
    .on('mouseover', curry(lineOver)(options, selectors, accessors, scales))
    .on('mouseout', curry(lineOut)(options, selectors, accessors, scales));
}

function hoverlineUpdate(selection, options, accessors, { xScale, yScale }) {
  lineUpdate(selection, options, accessors, { xScale, yScale });
  selection.style({ stroke: 'black', 'stroke-width': options.line.shadow, opacity: 0 });
}

function markerEnter(selection, options, selectors, accessors, scales) {
  setupElement(selection, 'svg:path', selectors.marker)
    .on('mouseover', function(datum) {
      const hoverline = d3.select(this.parentNode).select(selector(selectors.hoverline)).node();
      bind(lineOver, hoverline)(
        options, selectors, accessors, scales, d3.select(this.parentNode).datum()
      );
      if (accessors.isCondensed(options, d3.select(this.parentNode).datum(), scales.xScale)) {
        d3.select(this).style({
          opacity: 1,
          fill: function(datum) {
            return getOverColor(
              options, accessors.focusData(d3.select(this.parentNode).datum())
            );
          }
        });
      }
    })
    .on('mouseout', function(datum) {
      const hoverline = d3.select(this.parentNode).select(selector(selectors.hoverline)).node();
      bind(lineOut, hoverline)(
        options, selectors, accessors, scales, d3.select(this.parentNode).datum()
      );
      if (accessors.isCondensed(options, d3.select(this.parentNode).datum(), scales.xScale)) {
        d3.select(this).style({
          opacity: 0,
          fill: function(datum) {
            return getColor(
              options.serie, accessors.focusData(d3.select(this.parentNode).datum())
            );
          }
        });
      }
    })
    .on('mousemove', function(datum) {
      const serie = d3.select(this.parentNode).datum();
      const color = getOverColor(options, accessors.focusData(serie));
      showTooltip(selectors, accessors, options, scales, serie, datum, color);
    })
    .on('mouseleave', curry(hideTooltip)(selectors));
}

function markerUpdate(selection, options, accessors, { xScale, yScale }) {
  selection
    .attr({
      d: d3.svg.symbol().type(options.serie.line.marker.shape).size(30),
      transform: datum => `translate(${xScale(accessors.x(datum))}, ${yScale(accessors.y(datum))})`
    })
    .style({
      opacity: function(datum) {
        const serie = d3.select(this.parentNode).datum();
        const isFocused = accessors.isFocused(serie);
        const isCondensed = accessors.isCondensed(options.serie, serie, xScale);
        return isFocused && !isCondensed ? 1 : 0;
      },
      fill: function(datum) {
        return getColor(options.serie, accessors.focusData(d3.select(this.parentNode).datum()));
      },
      stroke: options.background.color,
      'stroke-width': options.serie.line.marker.shadow,
    })
}

function annotationDatapointEnter(selection, options, selector) {
  setupElement(selection, 'svg:text', selector);
}

function annotationDatapointBaseline(datum) {
  const element = d3.select(this);
  const offset = fontBaselineOffset(element, TEXT_ABOVE);
  return `translate(0, ${offset})`;
}

function annotationDatapointUpdate(selection, options, accessors, { xScale, yScale }) {
  selection
    .text(curry(accessors.annotationDatapoint)(options.serie.annotation))
    .attr({
      x: datum => xScale(accessors.x(datum)),
      y: datum => yScale(accessors.y(datum))
    })
    .style({
      ...fontStyle(options.serie.annotation.font),
      'text-anchor': TEXT_MIDDLE,
      'text-shadow': `1px 1px 0 ${options.background.color}, 0 0 2px ${options.background.color}`,
      opacity: 0
    });

  // after first rendering to have the correct height
  selection.attr({ transform: annotationDatapointBaseline });
}

function annotationSerieEnter(selection, options, selector) {
  setupElement(selection, 'svg:text', selector);
}

function annotationSerieBaseline(datum) {
  const element = d3.select(this);
  const offset = fontBaselineOffset(element, TEXT_MIDDLE);
  return `translate(0, ${offset})`;
}

function annotationSerieUpdate(selection, options, accessors, { xScale, yScale }) {
  selection
    .text(curry(accessors.annotationSerie)(options.serie.annotation))
    .attr({
      x: serie => xScale(accessors.x(accessors.lastDatapoint(serie))) + ANNOTATION_SERIE_OFFSET,
      y: serie => yScale(accessors.y(accessors.lastDatapoint(serie)))
    })
    .style({
      ...fontStyle(options.serie.annotation.font),
      fill: serie => getColor(options.serie, accessors.focusData(serie)),
      'text-anchor': TEXT_START,
      opacity: function(serie) {
        const annotationMaxWidth = options.serie.annotation.format.serie.maxWidth;
        const annotationWidth = getElementDimensions(d3.select(this)).width;
        return (accessors.isFocused(d3.select(this).datum()) && annotationWidth <= annotationMaxWidth) ? 1 : 0;
      }
    });

  // after first rendering to have the correct height
  selection.attr({ transform: annotationSerieBaseline });
}

export default function eauors(target) {
  target.eauors = {
    ...target.eauors,
    lineEnter,
    lineUpdate,
    hoverlineEnter,
    hoverlineUpdate,
    markerEnter,
    markerUpdate,
    annotationDatapointEnter,
    annotationDatapointUpdate,
    annotationSerieEnter,
    annotationSerieUpdate
  };
}



