import d3 from 'd3';
import { selector, getOverColor, getElementDimensions, getRangeDelta } from './';
import { TOOLTIP_CURSOR_X_OFFSET, TOOLTIP_CURSOR_Y_OFFSET, VISIBLE, HIDDEN } from './constants';

export function showTooltip(selectors, accessors, options, scales, serie, datum, color) {
  const element = d3.select(selector(selectors.tooltip));

  const _color = color || getOverColor(options, accessors.focusData(datum));
  element.html(options.tooltip.layout(serie, datum, _color));

  const limitX = getRangeDelta(scales.xScale.range());
  const limitY = getRangeDelta(scales.yScale.range());

  function placementEngine(element, { limitX, limitY }) {
    const [ mouseX, mouseY ] = d3.mouse(d3.select(selector(selectors.chart)).node());

    const { width, height } = element.node().getBoundingClientRect();

    const left = limitX > mouseX + width + TOOLTIP_CURSOR_X_OFFSET
      ? mouseX + TOOLTIP_CURSOR_X_OFFSET
      : mouseX - width - TOOLTIP_CURSOR_X_OFFSET / 2;

    const top = limitY > mouseY + height + TOOLTIP_CURSOR_Y_OFFSET
      ? mouseY + TOOLTIP_CURSOR_Y_OFFSET
      : mouseY - height - TOOLTIP_CURSOR_Y_OFFSET / 2;

    element.style({
      position: 'absolute',
      left: `${left}px`,
      top: `${top}px`,
      visibility: VISIBLE
    });
  };

  placementEngine(element, { limitX, limitY });
}

export function hideTooltip(selectors, partial) {
  d3.select(selector(selectors.tooltip)).style({ visibility: HIDDEN });
}



