import { HIDDEN } from '../utils/constants';

function wrapperUpdate(selection, options) {
  selection.style({
    position: 'relative',
    width: options.width+'px',
    height: options.height+'px',
  });
}

function chartUpdate(selection, options) {
  selection.attr({
    width: options.width + options.margin * 2,
    height: options.height + options.margin * 2
  });
}

function containerUpdate(selection, options) {
  selection.attr(
    'transform',
    `translate(${options.margin}, ${options.margin})`
  );
}

function tooltipUpdate(selection, options) {
  selection.style({ visibility: HIDDEN });
}

export default function eauors(target) {
  target.eauors = {
    ...target.eauors,
    wrapperUpdate,
    chartUpdate,
    containerUpdate,
    tooltipUpdate
  };
}



