import { get } from 'lodash';
import { BOTTOM } from '../utils/constants';

export default function accessors(target) {
  target.accessors = {
    ...target.accessors,
    format: (options) => get(options, 'legend.choropleth.axis.format'),
    height: (options) => get(options, 'legend.choropleth.height', null),
    orient: (options) => get(options, 'legend.choropleth.axis.orient', BOTTOM),
    margin: (options) => get(options, 'legend.choropleth.margin'),
    tickSize: (options) => get(options, 'legend.choropleth.axis.tick.size'),
    width: (options) => get(options, 'legend.choropleth.width', null)
  }
}


